import { Box, Button, Card, CardBody, Text } from "grommet";
import { Link, useParams } from "react-router-dom";
import sendTo from "./sendTo";
import { useCookies } from "react-cookie";

let hometext = require("../text/homepage.json");

export default function HomeSites() {
  let params = useParams();
  const [cookies, setCookie] = useCookies();
  console.log("Home Sites Rendered");
  return (
    <Box>
      <Card border="small">
        <CardBody pad="medium">
          {[
            { name: "Lavender", site: "lavender", bgcolor: "#69359c" },
            { name: "Distillery", site: "distillery", bgcolor: "#9c6935" },
            { name: "Dome", site: "dome", bgcolor: "silver" },
            { name: "Distance Pole", site: "distance", bgcolor: "#689c35" },
            { name: "Wildflowers", site: "wildflowers", bgcolor: "#36359c" },
            { name: "Herb Garden", site: "herb", bgcolor: "#359c69" }
          ].map((site) => (
            <li key={`${params.language}.${site.site}`}>
              <Link
                className="linkcomp"
                to={`/${params.language}/site/${site.site}`}
                onClick={() =>
                  sendTo(cookies.guest, `/${params.language}/site/${site.site}`)
                }
              >
                <Box
                  round="small"
                  pad={{ vertical: "medium", horizontal: "medium" }}
                  background={site.bgcolor}
                  margin="xxsmall"
                >
                  <Text color="white" size="medium">
                    {hometext[params.language][site.site]}
                  </Text>
                </Box>
              </Link>
            </li>
          ))}
        </CardBody>
      </Card>
    </Box>
  );
}
