import { useParams, Link, useLocation } from "react-router-dom";
import {
  PageHeader,
  Anchor,
  Button,
  Main,
  Heading,
  Paragraph,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Box,
  Text
} from "grommet/components";
import sendTo from "./sendTo";
import { useCookies } from "react-cookie";

export default function SwitchLanguage() {
  let location = useLocation();
  let pathname = location.pathname.split("/").splice(2, 5).join("/");
  const [cookies, setCookie] = useCookies();
  return (
    <div>
      <br />
      <Card width="100%" background="switchlangcard" border="small">
        <CardHeader pad="medium">Switch Language</CardHeader>
        <CardBody pad="medium">
          <Link
            to={`/en/${pathname}`}
            onClick={() => sendTo(cookies.guest, "SW_EN")}
          >
            <Box margin="xsmall">
              <Text color="black" margin="xsmall">
                English
              </Text>
            </Box>
          </Link>
          <Link
            to={`/pl/${pathname}`}
            onClick={() => sendTo(cookies.guest, "SW_PL")}
          >
            <Box margin="xsmall">
              <Text color="black" margin="xsmall">
                Polish
              </Text>
            </Box>
          </Link>
          <Link
            to={`/cn/${pathname}`}
            onClick={() => sendTo(cookies.guest, "SW_CN")}
          >
            <Box margin="xsmall">
              <Text color="black" margin="xsmall">
                Chinese
              </Text>
            </Box>
          </Link>
        </CardBody>
      </Card>
    </div>
  );
}
