import { Anchor, Box, Heading } from "grommet";
import { Console } from "grommet-icons";

import {
  Distribution,
  Text,
  Accordion,
  Grid,
  PageHeader,
  AccordionPanel,
  Video
} from "grommet/components";

import { useParams } from "react-router-dom";

let scents = require("../text/scents.json");

export default function Scents() {
  let params = useParams();
  let scentsuse = scents[params.language][params.pagename];

  return (
    <Accordion>
      {scentsuse.map((scent) => (
        <AccordionPanel label={scent.name}>
          <li key={scent.name}>
            <Box pad="medium" background="dark-2">
              <Text>{scent.text}</Text>
            </Box>
          </li>
        </AccordionPanel>
      ))}
    </Accordion>
  );
}
