import PageTemplate from "./components/pageTemplate";
import { Route, Routes, useParams, Outlet } from "react-router-dom";
import HomePage from "./components/homePage";
import { Link, useLocation } from "react-router-dom";
import SwitchLanguage from "./components/SwitchLanguage";
import { Grommet, ResponsiveContext, Box, Text, Button } from "grommet";
import Welcome from "./components/Welcome";
import SustainabilityPage from "./components/SustainabilityPage";
import "./styles.css";
import { useCookies } from "react-cookie";
import { useEffect, useState } from "react";

export default function App() {
  let params = useParams();
  let location = useLocation();
  let pathname = location.pathname.split("/").splice(2, 5).join("/");

  const theme = {
    global: {
      font: {
        family: "Roboto, Arial",
        size: "18px",
        height: "20px"
      },
      colors: {
        // Overriding existing grommet colors
        brand: "#8544A8",
        "accent-1": "#653E5B",
        "accent-2": "#C3260E",
        "accent-3": "#C3260E",
        "accent-4": "#4B41B6",
        "neutral-1": "#6A8336",
        "neutral-2": "#C3260E",
        "neutral-3": "#C3260E",
        "neutral-4": "#4B41B6",
        enter: "#4B41B6",
        switchlang: "#F8AF25",
        switchlangcard: "#FFF7EA"
      }
    }
  };

  const [user, setUser] = useState();

  const [cookies, setCookie] = useCookies(["guest"]);

  function setNewCookie(x) {
    setCookie("guest", x, {
      path: "/"
    });
  }

  useEffect(() => {
    if (cookies.guest) {
      setUser(cookies.guest);
    } else {
      setNewCookie(Math.floor(Math.random() * 10000000));
      setUser(cookies.guest);
    }
  });

  return (
    <div style={{ backgroundColor: "#111122" }}>
      <Grommet
        style={{ backgroundColor: "white" }}
        className="App"
        theme={theme}
      >
        <Box
          background="neutral-4"
          pad="small"
          color="white"
          style={{ zIndex: "1" }}
          elevation="small"
        >
          <Text color="white" alignSelf="center">
            #ScottishLavenderOils
          </Text>
        </Box>
        <Routes>
          <Route path="/" element={<Welcome />} />
          <Route path=":language/home" element={<HomePage />} />
          <Route path=":language/site/:pagename" element={<PageTemplate />} />
          <Route
            path=":language/sustainability/:pagename"
            element={<SustainabilityPage />}
          />

          <Route
            path="*"
            element={
              <div>
                <p>Cannot find the page.</p>
                <Link to={`/en/home`}>Return Home</Link>
              </div>
            }
          />
        </Routes>
        <Outlet />
        <p>v 1.4</p>
      </Grommet>
    </div>
  );
}
