import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import ReactPlayer from "react-player/vimeo";

import {
  Accordion,
  Card,
  CardHeader,
  CardBody,
  Button,
  AccordionPanel,
  Text,
  Box,
  Video,
  Anchor,
  PageHeader,
  Main,
  Carousel,
  Image,
  CardFooter,
  Heading
} from "grommet";
import Scents from "./Scents";
import SwitchLanguage from "./SwitchLanguage";
import { useCookies } from "react-cookie";
import { useEffect } from "react";
import sendTo from "./sendTo";

let text = require("../text/sitetext.json");
let commontext = require("../text/sitetextcommon.json");

export default function PageTemplate(props) {
  const [cookies, setCookie] = useCookies(["guest"]);

  let params = useParams();
  let textuse = text[params.language][params.pagename];
  let commontextuse = commontext[params.language];
  useEffect(() => {
    sendTo(cookies.guest, `${params.language}.${params.pagename}.`);
  });
  return (
    <Main>
      <Box background="dark-3">
        <Text alignSelf="center">
          <Link to={`/${params.language}/home`}>
            <Button
              secondary
              label={commontextuse.returnhome}
              margin="xsmall"
            />
          </Link>
        </Text>
      </Box>
      <Box>
        <Box pad="medium">
          <Video
            controls="over"
            fit="cover"
            onPlay={() =>
              sendTo(cookies.guest, `VID_${params.language}.${params.pagename}`)
            }
          >
            <source
              key="video"
              src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/Classic_Video_Final.mp4"
            />
            <track
              key="cc"
              label="English"
              kind="subtitles"
              srcLang="en"
              src="/assets/small-en.vtt"
              default
            />
          </Video>
        </Box>
        <PageHeader
          title={textuse.title}
          subtitle={textuse.subtitle}
          parent={<Anchor label={textuse.purpletext} />}
        />

        <Accordion>
          <AccordionPanel label={commontextuse.guidetext}>
            <Box pad="medium" border="small">
              <Text>
                {textuse.text.map((para) => (
                  <li key={para}>
                    <p>{para}</p>
                  </li>
                ))}
              </Text>
            </Box>
          </AccordionPanel>
        </Accordion>
      </Box>
      <br />

      <Box>
        <PageHeader
          title={commontextuse.photostitle}
          subtitle={commontextuse.photostext}
          parent={<Anchor label={commontextuse.joinin} />}
        />
        <Carousel fill controls="arrows" play="2000">
          {textuse.image.map((url) => (
            <Image fit="contain" src={url} />
          ))}
        </Carousel>
      </Box>

      <br />
      <Box>
        <PageHeader
          title={commontextuse.scenttour}
          subtitle={commontextuse.scenttourtext}
          parent={<Anchor label={commontextuse.followthescenttrail} />}
        />
        <Scents />
      </Box>
      <SwitchLanguage />
    </Main>
  );
}
