import {
  Accordion,
  AccordionPanel,
  Anchor,
  Box,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Distribution,
  Heading,
  PageHeader,
  Text,
  Video
} from "grommet/components";

import { Link, useParams } from "react-router-dom";

import SustainabilityNumbers from "./SustainabilityNumbers";

let sustext = require("../text/sustainability.json");

export default function Sustainability() {
  let params = useParams();
  let textuse = sustext[params.language];

  return (
    <Box>
      <Card border="small">
        <CardBody pad="medium">
          {[
            { name: "lochan", link: "/", bgcolor: "#69359c", color: "white" },
            {
              name: "animal",
              link: "/",
              bgcolor: "#689c35",
              color: "white"
            },

            {
              name: "maintenance",
              link: "/",
              bgcolor: "#9c359c",
              color: "white"
            },
            { name: "trees", link: "/", bgcolor: "#359c69", color: "white" }
          ].map((site) => (
            <li key={`${params.language}.${site.name}`}>
              <Link
                className="linkcomp"
                to={`/${params.language}/sustainability/${site.name}`}
              >
                <Box
                  round="small"
                  pad={{ vertical: "medium", horizontal: "medium" }}
                  background={site.bgcolor}
                  margin="xxsmall"
                >
                  <Text color={site.color}>{textuse[site.name].name}</Text>
                </Box>
              </Link>
            </li>
          ))}
        </CardBody>
      </Card>
    </Box>
  );
}
