import {
  Box,
  Text,
  Button,
  Main,
  Heading,
  Carousel,
  Image,
  Video
} from "grommet";
import { useEffect } from "react";
import { useCookies } from "react-cookie";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import sendTo from "./sendTo";
import SwitchLanguage from "./SwitchLanguage";
let commontext = require("../text/sitetextcommon.json");
let sustext = require("../text/sustainability.json");

export default function SustainabilityPage() {
  const [cookies, setCookie] = useCookies(["guest"]);

  let params = useParams();
  let commontextuse = commontext[params.language];
  let textuse = sustext[params.language][params.pagename];

  useEffect(() => {
    sendTo(cookies.guest, `${params.language}.${params.pagename}.`);
  });

  return (
    <Main>
      <Box background="dark-3">
        <Text alignSelf="center">
          <Link to={`/${params.language}/home`}>
            <Button
              secondary
              label={commontextuse.returnhome}
              margin="xsmall"
            />
          </Link>
        </Text>
      </Box>

      <br />

      <Box height="small" width="medium" overflow="hidden">
        <Carousel fill play="3000">
          <Image
            fit="cover"
            src="//v2.grommet.io/assets/Wilderpeople_Ricky.jpg"
          />
          <Image fit="cover" src="//v2.grommet.io/assets/IMG_4245.jpg" />
          <Image fit="cover" src="//v2.grommet.io/assets/IMG_4210.jpg" />
        </Carousel>
      </Box>

      <Heading margin={{ bottom: "small" }}>{textuse.name}</Heading>
      {textuse.text.map((sentence) => (
        <li key={sentence.substring(0, 10)}>
          <Box>
            <Text>{sentence}</Text>
            <br />
          </Box>
        </li>
      ))}
      <br />

      <SwitchLanguage />
    </Main>
  );
}
