import { Page, ResponsiveContext } from "grommet";
import {
  PageHeader,
  Anchor,
  Button,
  Main,
  Carousel,
  Image,
  Card,
  Text,
  CardHeader,
  CardBody,
  Box,
  Video,
  Accordion,
  AccordionPanel
} from "grommet/components";

import { useParams } from "react-router-dom";
import HomeSites from "./Home_Sites";
import SustainabilityNumbers from "./SustainabilityNumbers";
import Sustainability from "./Sustainability";
import SwitchLanguage from "./SwitchLanguage";
import { Link } from "react-router-dom";
import sendTo from "./sendTo";
import { useCookies } from "react-cookie";
let hometext = require("../text/homepage.json");

export default function HomePage() {
  const [cookies, setCookies] = useCookies("guest");
  let params = useParams();

  return (
    <Main>
      <Box overflow={{ horizontal: "hidden" }}>
        <Box height="small" overflow="hidden">
          <Video
            style={{ backgroundColor: "#111111" }}
            controls="over"
            fit="contain"
            onPlay={() => sendTo(cookies.guest, `${params.language}.VID_HOME`)}
          >
            <source
              key="video"
              src={hometext[params.language]["welcomevideourl"]}
            />
            <track
              key="cc"
              label="English"
              kind="subtitles"
              srcLang="en"
              src="/assets/small-en.vtt"
              default
            />
          </Video>
        </Box>

        <PageHeader
          title={hometext[params.language]["welcome"]}
          subtitle={hometext[params.language]["summerscent"]}
          parent={<Anchor label="Scottish Lavender Oils" />}
        />
        <Accordion>
          <AccordionPanel label={hometext[params.language]["welcome!text"]}>
            <li key={hometext[params.language]["welcome!text"]}>
              <Box pad="medium" border="small">
                {hometext[params.language]["welcomepara"].map((sentence) => (
                  <Box>
                    <Text>{sentence}</Text> <br />
                  </Box>
                ))}
                <Text color="brand" weight="bold">
                  {hometext[params.language]["welcomeparaselfie"]}
                </Text>
              </Box>
            </li>
          </AccordionPanel>

          <AccordionPanel label={hometext[params.language]["tour"]}>
            <HomeSites />
            <br />
          </AccordionPanel>

          <AccordionPanel label={hometext[params.language]["sustainability"]}>
            <Sustainability />
          </AccordionPanel>
        </Accordion>

        <SwitchLanguage />
      </Box>
    </Main>
  );
}
